html {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  *, *:before, *:after {
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
  }
  
  body {
    margin: 0;
    font-size: 14px;
    font-weight: 100;
    font-family: -apple-system,
                 BlinkMacSystemFont,
                 "Segoe UI",
                 Roboto,
                 Helvetica,
                 Arial,
                 sans-serif;
  }
  
  button {
    display: block;
    background: none;
    border: none;
    padding: 0;
    font-weight: 200;
    font-family: -apple-system,
                  BlinkMacSystemFont,
                  "Segoe UI",
                  Roboto,
                  Helvetica,
                  Arial,
                  sans-serif;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    outline: none;
    border-radius: 0px;
  
    -webkit-tap-highlight-color: rgba(0,0,0,0);
  }
  
  button:active {
    -webkit-box-shadow: inset 0px 0px 0px 100px rgba(0,0,0,0.15);
            box-shadow: inset 0px 0px 0px 100px rgba(0,0,0,0.15);
  }

  .function-keys button {
      font-weight: 100;
  }

  button img {
      width: 41%;
      margin-top: 28px;
  }
  
  #root {
    height: 100vh;
  
    display: -ms-flexbox;
  
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
  }
  
  #wrapper {
    width: 320px;
    height: 520px;
    /* height: 550px; */
    position: relative;
  }
  
  .calculator {
    width: 100%;
    height: 100%;
    background: black;
  
    display: -ms-flexbox;
  
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  
    -webkit-box-shadow: 0px 0px 20px 0px #aaa;
  
            box-shadow: 0px 0px 20px 0px #aaa;
  }
  
  .calculator-display {
    color: white;
    background: #1c191c;
    line-height: 120px;
    font-size: 6em;
  
    padding: 0 15px;
    /* remove line-height */
    /* padding-top: 40px; */
    text-align: right;
  
    -ms-flex: 1 1;
  
        flex: 1 1;
  }
  
  .auto-scaling-text {
    padding: 0 30px;
  
    position: absolute;
    right: 0;
    -webkit-transform-origin: right;
        -ms-transform-origin: right;
            transform-origin: right;
  }
  
  .calculator-keypad {
    height: 400px;
  
    display: -ms-flexbox;
  
    display: flex;
  }
  
  .calculator .input-keys {
    width: 240px;
  }
  
  .calculator .function-keys {
    display: -ms-flexbox;
    display: flex;
  }
  
  .calculator .digit-keys {
    background: rgb(220, 222, 225);
  
    display: -ms-flexbox;
  
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap-reverse;
        flex-wrap: wrap-reverse;
  }
  
  .calculator-key {
    width: 80px;
    height: 80px;
    border-top: 1px solid rgba(0,0,0,0.25);
    border-right: 1px solid rgba(0,0,0,0.25);
    text-align: center;
    line-height: 80px;
  }
  .calculator .function-keys .calculator-key {
    font-size: 2em;
  }
  .calculator .function-keys .key-multiply {
    line-height: 50px;
  }
  .calculator .digit-keys .calculator-key {
    font-size: 2.25em;
  }
  .calculator .digit-keys .key-0 {
    width: 160px;
    text-align: left;
    padding-left: 32px;
  }
  .calculator .digit-keys .key-dot {
    font-size: 3em;
  }
  .calculator .operator-keys .calculator-key {
    color: white;
    border-right: 0;
    /* border: 0;
    border-bottom: 1px solid rgba(0,0,0,0.25);  */
    font-size: 3em;
    -webkit-transition: border 0.1s;
    -o-transition: border 0.1s;
    transition: border 0.1s;
  }

  /* .calculator .operator-keys .calculator-key:first-child {
    border-top: 1px solid rgba(0,0,0,0.25);
  } */

  .calculator .operator-keys .calculator-key:focus {
    border: 1px solid black;
  }
  
  .calculator .function-keys {
    background: rgb(200, 202, 205);
  }
  .calculator .operator-keys {
    background:  -webkit-gradient(linear, left top, left bottom, from(rgba(252,156,23,1)), to(rgba(247,126,27,1)));
    background:  -webkit-linear-gradient(top, rgba(252,156,23,1) 0%, rgba(247,126,27,1) 100%);
    background:  -o-linear-gradient(top, rgba(252,156,23,1) 0%, rgba(247,126,27,1) 100%);
    background:  linear-gradient(to bottom, rgba(252,156,23,1) 0%, rgba(247,126,27,1) 100%);
  }


